import { Bread } from './Bread';

export const Duck = ({ x, y, speed, angle, hasBread }) => {
    return <g transform={`translate (${x} ${y}) rotate(${angle} ${0} ${0})`}>
        {hasBread ? <Bread x={0} y={40} age={0}/> : <g/>}
        
        <ellipse cx={0} cy={0} rx={15} ry={20} fill="grey" />
        {/* <circle cx={0} cy={0} r={100} stroke="green" fill="none"/> */}
        {/* <circle cx={0} cy={0} r={40} stroke="purple" fill="none"/> */}
        <ellipse cx={0} cy={0 + 24} fill="yellow" rx={7} ry={14} />
        <circle cx={0} cy={0 + 16} fill="green" r="14" />
        <circle cx={0 - 4} cy={0 + 19} fill="#222" r="3" />
        <circle cx={0 + 4} cy={0 + 19} fill="#222" r="3" />

    </g>
}